const Button = styled(motion.button)`
  border: none;
  border-radius: ${props => props.theme.borderRadius.lg};
  padding: ${props => props.theme.spacing.sm} ${props => props.theme.spacing.lg};
  cursor: pointer;
  font-size: ${props => props.theme.typography.fontSize.sm};
  font-weight: ${props => props.theme.typography.fontWeight.medium};
  transition: all ${props => props.theme.transitions.normal};
  
  ${props => {
    switch(props.variant) {
      case 'primary':
        return `
          background: ${props.theme.colors.gradients.highlight};
          color: ${props.theme.colors.text.primary};
          padding: ${props.theme.spacing.lg} ${props.theme.spacing['2xl']};
          font-size: ${props.theme.typography.fontSize.lg};
          width: 100%;
        `;
      case 'danger':
        return `
          background: rgba(255, 107, 107, 0.2);
          border: 1px solid #ff6b6b;
          color: #ff6b6b;
        `;
      case 'admin':
        return `
          background: rgba(255, 215, 0, 0.2);
          border: 2px solid #ffd700;
          color: #ffd700;
          padding: ${props.theme.spacing.lg};
          width: 100%;
        `;
      default:
        return `
          background: ${props.theme.colors.gradients.accent};
          border: 1px solid ${props.theme.colors.text.accent};
          color: ${props.theme.colors.text.primary};
        `;
    }
  }}
  
  &:hover:not(:disabled) {
    transform: translateY(-2px);
    ${props => props.variant === 'primary' && `box-shadow: ${props.theme.shadows.neon};`}
  }
  
  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
`;

