// Alert components
const Alert = styled.div`
  border-radius: 12px;
  padding: 16px;
  margin-bottom: 24px;
  text-align: center;
  
  ${props => props.type === 'error' ? `
    background: rgba(255, 107, 107, 0.1);
    border: 1px solid #ff6b6b;
    color: #ff6b6b;
  ` : `
    background: rgba(34, 197, 94, 0.1);
    border: 1px solid #22c55e;
    color: #22c55e;
  `}
`;

const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:3000';

const CVHacker = ({ isModal = false, onClose = null }) => {
  const { user, token, csrfToken, logout, isAuthenticated } = useAuth();
  const [showAuthModal, setShowAuthModal] = useState(false);
  const [selectedFile, setSelectedFile] = useState(null);
  const [jobDescription, setJobDescription] = useState('');
  const [isDragOver, setIsDragOver] = useState(false);
  const [isUploading, setIsUploading] = useState(false);
  const [submissions, setSubmissions] = useState([]);
  const [submissionCount, setSubmissionCount] = useState(0);
  const [optimizations, setOptimizations] = useState([]);
  const [promptType, setPromptType] = useState('normal');
  const [isOptimizing, setIsOptimizing] = useState(false);
  const [optimizationCount, setOptimizationCount] = useState(0);
  const [selectedExistingCV, setSelectedExistingCV] = useState('');
  const [existingCVs, setExistingCVs] = useState([]);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [analysisModal, setAnalysisModal] = useState({ isOpen: false, taskId: null, filename: null });
  
  const maxSubmissions = user?.isAdmin ? Infinity : 3;
  const maxOptimizations = user?.isAdmin ? Infinity : 5;

  // Centralized API call function
  const apiCall = useCallback(async (endpoint, options = {}) => {
    const defaultHeaders = {
      'Authorization': `Bearer ${token}`,
      'Content-Type': 'application/json',
      ...(csrfToken && { 'X-CSRF-Token': csrfToken })
    };
    
    const config = {
      credentials: 'include',
      headers: { ...defaultHeaders, ...options.headers },
      ...options
    };
    
    const response = await fetch(`${API_BASE_URL}${endpoint}`, config);
    return { response, data: await response.json() };
  }, [token, csrfToken]);

  // Centralized download function
  const downloadFile = useCallback(async (endpoint, filename) => {
    try {
      const response = await fetch(`${API_BASE_URL}${endpoint}`, {
        headers: { 'Authorization': `Bearer ${token}` },
        credentials: 'include'
      });

      if (response.ok) {
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = filename;
        a.style.display = 'none';
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        window.URL.revokeObjectURL(url);
        return true;
      }
      return false;
    } catch (error) {
      console.error('Download failed:', error);
      return false;
    }
  }, [token]);

  // Fetch functions
  const fetchSubmissions = useCallback(async () => {
    if (!token) return;

    try {
      const endpoint = user?.isAdmin 
        ? '/api/admin/users/cv-submissions'
        : '/api/upload/submissions';
        
      const { response, data } = await apiCall(endpoint);

      if (response.ok) {
        if (user?.isAdmin) {
          const allSubmissions = [];
          let totalCount = 0;
          
          if (data.success && data.data?.users) {
            data.data.users.forEach(userData => {
              userData.submissions.forEach(submission => {
                allSubmissions.push({
                  ...submission,
                  userEmail: userData.email,
                  userName: userData.name || 'Unknown'
                });
              });
              totalCount += userData.submissions.length;
            });
          }
          
          setSubmissions(allSubmissions);
          setSubmissionCount(totalCount);
        } else {
          setSubmissions(data.submissions || []);
          setSubmissionCount(data.submissions?.length || 0);
        }
      }
    } catch (error) {
      console.warn('Failed to fetch submissions:', error);
    }
  }, [token, user, apiCall]);

  const fetchOptimizations = useCallback(async () => {
    if (!token) return;

    try {
      const { response, data } = await apiCall('/api/cv-optimizer/optimizations');
      if (response.ok) {
        console.log('Fetched optimizations:', data.optimizations);
        setOptimizations(data.optimizations || []);
        setOptimizationCount(data.count || 0);
      } else {
        console.warn('Failed to fetch optimizations:', data);
      }
    } catch (error) {
      console.error('Error fetching optimizations:', error);
    }
  }, [token, apiCall]);

  const fetchExistingCVs = useCallback(async () => {
    if (!token) return;

    try {
      const { response, data } = await apiCall('/api/upload/submissions');
      if (response.ok) {
        console.log('Fetched existing CVs:', data.submissions);
        setExistingCVs(data.submissions || []);
      } else {
        console.warn('Failed to fetch existing CVs:', data);
      }
    } catch (error) {
      console.error('Error fetching existing CVs:', error);
    }
  }, [token, apiCall]);

  // Event handlers
  const handleFileSelect = (file) => {
    const isValidType = file && (
      file.type === 'application/pdf' || 
      file.name.toLowerCase().endsWith('.tex') ||
      file.type === 'application/x-tex' ||
      file.type === 'text/x-tex'
    );
    
    if (isValidType) {
      if (file.size > 10 * 1024 * 1024) {
        setError('File size must be less than 10MB');
        return;
      }
      setSelectedFile(file);
      setError('');
    } else {
      setError('Please select a PDF or LaTeX (.tex) file');
    }
  };

  const handleDrop = (e) => {
    e.preventDefault();
    setIsDragOver(false);
    handleFileSelect(e.dataTransfer.files[0]);
  };

  const handleOptimize = async (e) => {
    e.preventDefault();
    
    // Check if either a file is selected or an existing CV is chosen
    if (!selectedFile && !selectedExistingCV) {
      setError('Please either select an existing CV or upload a new CV file');
      return;
    }
    
    if (selectedFile && selectedExistingCV) {
      setError('Please choose either an existing CV or upload a new file, not both');
      return;
    }
    
    if (!jobDescription.trim() || jobDescription.trim().length < 50) {
      setError('Job description must be at least 50 characters long');
      return;
    }

    if (!user?.isAdmin && optimizationCount >= maxOptimizations) {
      setError(`You have reached the maximum of ${maxOptimizations} optimizations`);
      return;
    }

    setIsOptimizing(true);
    setError('');
    setSuccess('');

    const formData = new FormData();
    
    if (selectedFile) {
      formData.append('cv_file', selectedFile);
    } else {
      formData.append('existing_cv_id', selectedExistingCV);
    }
    
    formData.append('job_description', jobDescription);
    formData.append('prompt_type', promptType);

    try {
      const response = await fetch(`${API_BASE_URL}/api/cv-optimizer/optimize`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          ...(csrfToken && { 'X-CSRF-Token': csrfToken })
        },
        credentials: 'include',
        body: formData
      });

      const data = await response.json();

      if (response.ok) {
        setSuccess('CV optimization started! You can track progress in the optimizations list below.');
        setSelectedFile(null);
        setSelectedExistingCV('');
        setJobDescription('');
        fetchOptimizations();
        fetchExistingCVs(); // Refresh existing CVs list since new CV might have been added
        
        const fileInput = document.getElementById('cv-file-input');
        if (fileInput) fileInput.value = '';
      } else {
        setError(data.error || 'Optimization failed');
      }
    } catch (error) {
      setError('Network error. Please try again.');
    } finally {
      setIsOptimizing(false);
    }
  };

  const handleDownload = async (submissionId, fileName) => {
    const endpoint = user?.isAdmin 
      ? `/api/admin/submissions/${submissionId}/download`
      : `/api/upload/submissions/${submissionId}/download`;
      
    const success = await downloadFile(endpoint, fileName);
    if (!success) setError('Failed to download file');
  };

  const handleDownloadJobDescription = async (submissionId, fileName) => {
    const endpoint = user?.isAdmin 
      ? `/api/admin/submissions/${submissionId}/job-description`
      : `/api/upload/submissions/${submissionId}/job-description`;
      
    const success = await downloadFile(endpoint, `${fileName.replace('.pdf', '')}_job_description.txt`);
    if (!success) setError('Failed to download job description');
  };

  const handleDelete = async (submissionId) => {
    if (!window.confirm('Are you sure you want to delete this submission?')) return;

    try {
      const endpoint = user?.isAdmin 
        ? `/api/admin/submissions/${submissionId}`
        : `/api/upload/submissions/${submissionId}`;
        
      const { response, data } = await apiCall(endpoint, { method: 'DELETE' });

      if (response.ok) {
        setSuccess('Submission deleted successfully');
        fetchSubmissions();
      } else {
        setError(data.error || 'Failed to delete submission');
      }
    } catch (error) {
      setError('Delete failed');
    }
  };

  const downloadOptimizedCV = async (taskId, filename) => {
    if (!taskId || taskId === 'undefined') {
      setError('Invalid task ID for download');
      return;
    }

    try {
      const { response, data } = await apiCall(`/api/cv-optimizer/result/${taskId}`);

      if (response.ok && data) {
        // Check if we have the optimized CV content
        const cvContent = data.optimized_cv_latex || data.cv_latex;
        
        if (cvContent && cvContent.trim()) {
          const blob = new Blob([cvContent], { type: 'text/plain' });
          const url = window.URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url;
          a.download = `optimized_${filename.replace(/\.[^/.]+$/, '')}.tex`;
          a.style.display = 'none';
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
          window.URL.revokeObjectURL(url);
          
          setSuccess('Optimized CV downloaded successfully!');
        } else {
          setError('Optimized CV not available. The LaTeX generation may have failed during processing.');
        }
      } else {
        setError(data?.error || 'Failed to download optimized CV');
      }
    } catch (error) {
      console.error('CV download error:', error);
      setError('Failed to download optimized CV');
    }
  };

  const downloadCoverLetter = async (taskId, filename) => {
    if (!taskId || taskId === 'undefined') {
      setError('Invalid task ID for download');
      return;
    }

    try {
      const { response, data } = await apiCall(`/api/cv-optimizer/result/${taskId}`);

      if (response.ok && data) {
        // Check if we have the cover letter content
        const coverContent = data.cover_letter_latex || data.cover_latex;
        
        if (coverContent && coverContent.trim()) {
          const blob = new Blob([coverContent], { type: 'text/plain' });
          const url = window.URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url;
          a.download = `cover_letter_${filename.replace(/\.[^/.]+$/, '')}.tex`;
          a.style.display = 'none';
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
          window.URL.revokeObjectURL(url);
          
          setSuccess('Cover letter downloaded successfully!');
        } else {
          setError('Cover letter not available. The LaTeX generation may have failed during processing.');
        }
      } else {
        setError(data?.error || 'Failed to download cover letter');
      }
    } catch (error) {
      console.error('Cover letter download error:', error);
      setError('Failed to download cover letter');
    }
  };

  const openAnalysisModal = (taskId, filename) => {
    setAnalysisModal({ isOpen: true, taskId, filename });
  };

  const closeAnalysisModal = () => {
    setAnalysisModal({ isOpen: false, taskId: null, filename: null });
  };

  const deleteOptimization = async (taskId) => {
    if (!taskId || taskId === 'undefined') {
      setError('Invalid task ID for deletion');
      return;
    }

    try {
      const { response } = await apiCall(`/api/cv-optimizer/task/${taskId}`, { method: 'DELETE' });
      if (response.ok) {
        setSuccess('Optimization deleted successfully');
        fetchOptimizations();
      } else {
        setError('Failed to delete optimization');
      }
    } catch (error) {
      setError('Failed to delete optimization');
    }
  };

  const handleDownloadAllCVs = async () => {
    const success = await downloadFile('/api/admin/download-all-cvs', `all-cvs-${new Date().toISOString().split('T')[0]}.zip`);
    if (success) {
      setSuccess('All CVs downloaded successfully');
    } else {
      setError('Failed to download all CVs');
    }
  };

  const handleDeleteAllCVs = async () => {
    if (!window.confirm(' Are you sure you want to delete ALL CVs from ALL users? This action cannot be undone!')) return;
    if (!window.confirm(' FINAL WARNING: This will permanently delete all user CVs. Type YES in the next prompt to confirm.')) return;

    const confirmation = window.prompt('Type "DELETE ALL CVS" to confirm:');
    if (confirmation !== 'DELETE ALL CVS') {
      setError('Deletion cancelled - confirmation text did not match');
      return;
    }

    try {
      const { response, data } = await apiCall('/api/admin/delete-all-cvs', { method: 'DELETE' });
      if (response.ok) {
        setSuccess(`Successfully deleted ${data.data.deletedFiles} files and ${data.data.deletedRecords} records`);
        setSubmissions([]);
        setSubmissionCount(0);
        fetchSubmissions();
      } else {
        setError(data.error || 'Failed to delete all CVs');
      }
    } catch (error) {
      setError('Failed to delete all CVs');
    }
  };

  // Effects
  useEffect(() => {
    if (!isAuthenticated) {
      setShowAuthModal(true);
    }
  }, [isAuthenticated]);

  useEffect(() => {
    if (isAuthenticated) {
      fetchSubmissions();
      fetchOptimizations();
      fetchExistingCVs();
    }
  }, [isAuthenticated, fetchSubmissions, fetchOptimizations, fetchExistingCVs]);

  useEffect(() => {
    if (isAuthenticated && user?.isAdmin) {
      const interval = setInterval(fetchSubmissions, 30000);
      return () => clearInterval(interval);
    }
  }, [isAuthenticated, user?.isAdmin, fetchSubmissions]);

  useEffect(() => {
    if (optimizations.some(opt => opt.status === 'pending' || opt.status === 'processing')) {
      const interval = setInterval(fetchOptimizations, 5000);
      return () => clearInterval(interval);
    }
  }, [optimizations, fetchOptimizations]);

  // Render functions
  const renderAuthContent = () => (
    <Container $isModal={isModal}>
      <Header>
        <Title
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
           CV Hacker
        </Title>
        <Subtitle
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
        >
          Please log in to access CV Hacker
        </Subtitle>
      </Header>
      <AuthModal
        isOpen={showAuthModal}
        onClose={() => setShowAuthModal(false)}
        onSuccess={() => setShowAuthModal(false)}
      />
    </Container>
  );

  const renderMainContent = () => (
    <Container $isModal={isModal}>
      <Header>
        <Title
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
           CV Hacker
        </Title>
        <Subtitle
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
        >
          AI-powered CV optimization with Claude AI
        </Subtitle>
      </Header>

      <GlassCard style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <div style={{ color: '#fff', fontWeight: 600 }}>
           Welcome back, {user?.email}
        </div>
        <Button onClick={logout} whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}>
          Logout
        </Button>
      </GlassCard>

      <GlassCard
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8, delay: 0.4 }}
        style={{ maxWidth: '800px', margin: '0 auto' }}
      >
        {error && <Alert type="error">{error}</Alert>}
        {success && <Alert type="success">{success}</Alert>}

        <form onSubmit={handleOptimize}>
          {/* Existing CVs Dropdown - always visible */}
          <FormSection>
            <SectionTitle> Select Existing CV</SectionTitle>
            <Select value={selectedExistingCV} onChange={(e) => setSelectedExistingCV(e.target.value)}>
              <option value="">Choose an existing CV...</option>
              {existingCVs.map((cv) => (
                <option key={cv.id} value={cv.id}>
                  {cv.originalName} ({new Date(cv.uploadedAt).toLocaleDateString()})
                </option>
              ))}
            </Select>
          </FormSection>

          {/* Upload Zone - always visible */}
          <FormSection>
            <SectionTitle> Upload New CV (PDF or LaTeX)</SectionTitle>
            <UploadZone
              isDragOver={isDragOver}
              onDrop={handleDrop}
              onDragOver={(e) => { e.preventDefault(); setIsDragOver(true); }}
              onDragLeave={(e) => { e.preventDefault(); setIsDragOver(false); }}
              onClick={() => document.getElementById('cv-file-input').click()}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
            >
              <div style={{ fontSize: '3rem', marginBottom: '16px' }}></div>
              <div style={{ color: '#fff', fontSize: '18px', marginBottom: '8px' }}>
                {selectedFile ? selectedFile.name : 'Drop your CV here or click to browse'}
              </div>
              <div style={{ color: '#94a3b8', fontSize: '14px' }}>
                PDF or LaTeX files, max 10MB
              </div>
              <input
                id="cv-file-input"
                type="file"
                accept=".pdf,.tex"
                onChange={(e) => handleFileSelect(e.target.files[0])}
                style={{ display: 'none' }}
              />
            </UploadZone>
            {selectedFile && (
              <div style={{ 
                background: 'rgba(255, 255, 255, 0.1)', 
                padding: '16px', 
                borderRadius: '12px',
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center'
              }}>
                <span style={{ color: '#fff', fontWeight: 600 }}> {selectedFile.name}</span>
                <Button variant="danger" onClick={() => setSelectedFile(null)}>Remove</Button>
              </div>
            )}
          </FormSection>

          <FormSection>
            <SectionTitle> Job Description *</SectionTitle>
            <TextInput
              value={jobDescription}
              onChange={(e) => setJobDescription(e.target.value)}
              placeholder="Paste the job description here. This helps our AI optimize your CV for this specific role..."
              required
            />
          </FormSection>

          {/* Optimization Mode - moved here, just above the button */}
          <FormSection>
            <label style={{ color: '#fff', marginBottom: '8px', display: 'block' }}>Optimization Mode</label>
            <RadioGroup>
              <RadioOption>
                <input
                  type="radio"
                  name="promptType"
                  value="normal"
                  checked={promptType === 'normal'}
                  onChange={(e) => setPromptType(e.target.value)}
                />
                <div>
                  <div style={{ fontWeight: '600', marginBottom: '4px' }}> Normal</div>
                  <div style={{ fontSize: '14px', color: '#94a3b8' }}>Professional, targeted optimization</div>
                </div>
              </RadioOption>
              <RadioOption>
                <input
                  type="radio"
                  name="promptType"
                  value="unhinged"
                  checked={promptType === 'unhinged'}
                  onChange={(e) => setPromptType(e.target.value)}
                />
                <div>
                  <div style={{ fontWeight: '600', marginBottom: '4px' }}> Unhinged</div>
                  <div style={{ fontSize: '14px', color: '#94a3b8' }}>Aggressive, maximum impact optimization</div>
                </div>
              </RadioOption>
            </RadioGroup>
          </FormSection>

          <Button
            variant="primary"
            type="submit"
            disabled={
              isOptimizing || 
              (!selectedFile && !selectedExistingCV) ||
              !jobDescription.trim() || 
              (!user?.isAdmin && optimizationCount >= maxOptimizations)
            }
            whileHover={!isOptimizing ? { scale: 1.02 } : {}}
            whileTap={!isOptimizing ? { scale: 0.98 } : {}}
          >
            {isOptimizing ? ' Optimizing...' : ' Optimize CV with AI'}
          </Button>
        </form>

        {/* Optimizations List */}
        {optimizations.length > 0 && (
          <div style={{ marginTop: '48px', paddingTop: '48px', borderTop: '1px solid rgba(255,255,255,0.1)' }}>
            <SectionTitle>
               Your CV Optimizations ({optimizationCount}/{user?.isAdmin ? '' : maxOptimizations})
            </SectionTitle>
            <AnimatePresence>
              {optimizations.map((optimization, index) => (
                <GlassCard
                  key={optimization.taskId || `optimization-${index}`}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                  transition={{ delay: index * 0.1 }}
                  style={{ 
                    background: 'rgba(255,255,255,0.05)',
                    display: 'flex',
                    flexDirection: 'column',
                    gap: '16px'
                  }}
                >
                  <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                    <div>
                      <h4 style={{ margin: 0, color: '#fff', marginBottom: '8px' }}>
                        {optimization.filename || 'CV Optimization'}
                      </h4>
                      <div style={{ color: '#94a3b8', fontSize: '14px' }}>
                        Mode: {optimization.promptType} | Created: {new Date(optimization.createdAt).toLocaleDateString()}
                      </div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                      {optimization.status === 'completed' ? (
                        <div
                          onClick={() => openAnalysisModal(optimization.taskId, optimization.filename)}
                          style={{
                            background: 'rgba(34, 197, 94, 0.2)',
                            border: '1px solid #22c55e',
                            borderRadius: '8px',
                            padding: '8px 12px',
                            cursor: 'pointer',
                            transition: 'all 0.2s ease',
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center',
                            minWidth: '120px'
                          }}
                          onMouseEnter={(e) => {
                            e.target.style.background = 'rgba(34, 197, 94, 0.3)';
                            e.target.style.transform = 'scale(1.05)';
                          }}
                          onMouseLeave={(e) => {
                            e.target.style.background = 'rgba(34, 197, 94, 0.2)';
                            e.target.style.transform = 'scale(1)';
                          }}
                        >
                          <div style={{
                            color: '#22c55e',
                            fontSize: '12px',
                            fontWeight: '600',
                            textTransform: 'uppercase',
                            lineHeight: '1'
                          }}>
                            Completed
                          </div>
                          <div style={{
                            color: '#22c55e',
                            fontSize: '10px',
                            marginTop: '2px',
                            lineHeight: '1'
                          }}>
                            (View ATS Report)
                          </div>
                        </div>
                      ) : (
                        <StatusBadge status={optimization.status}>
                          {optimization.status}
                        </StatusBadge>
                      )}
                    </div>
                  </div>
                  
                  <div style={{ display: 'flex', gap: '8px', flexWrap: 'wrap' }}>
                    {optimization.status === 'completed' && (
                      <>
                        <Button
                          onClick={() => downloadOptimizedCV(optimization.taskId, optimization.filename)}
                          whileHover={{ scale: 1.05 }}
                          whileTap={{ scale: 0.95 }}
                          style={{ fontSize: '12px', padding: '8px 12px' }}
                        >
                           Download CV
                        </Button>
                        <Button
                          onClick={() => downloadCoverLetter(optimization.taskId, optimization.filename)}
                          whileHover={{ scale: 1.05 }}
                          whileTap={{ scale: 0.95 }}
                          style={{ fontSize: '12px', padding: '8px 12px' }}
                        >
                           Cover Letter
                        </Button>
                      </>
                    )}
                    <Button
                      variant="danger"
                      onClick={() => deleteOptimization(optimization.taskId)}
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.95 }}
                      style={{ fontSize: '12px', padding: '8px 12px' }}
                    >
                       Delete
                    </Button>
                  </div>
                </GlassCard>
              ))}
            </AnimatePresence>
          </div>
        )}

        {/* Submissions List */}
        <div style={{ marginTop: '48px', paddingTop: '48px', borderTop: '1px solid rgba(255,255,255,0.1)' }}>
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '16px' }}>
            <SectionTitle>
              {user?.isAdmin 
                ? ` All User Submissions (${submissionCount} total)`
                : ` Your Submissions (${submissionCount}/${maxSubmissions})`
              }
            </SectionTitle>
            
            {user?.isAdmin && (
              <Button
                onClick={() => {
                  setSubmissions([]);
                  setSubmissionCount(0);
                  fetchSubmissions();
                }}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                 Refresh
              </Button>
            )}
          </div>
          
          {!user?.isAdmin && (
            <ProgressBar>
              <ProgressFill
                initial={{ width: 0 }}
                animate={{ width: `${(submissionCount / maxSubmissions) * 100}%` }}
                transition={{ duration: 0.8 }}
              />
            </ProgressBar>
          )}

          {submissions.length === 0 ? (
            <div style={{ 
              textAlign: 'center', 
              color: '#94a3b8', 
              padding: '32px', 
              fontStyle: 'italic' 
            }}>
              No submissions yet. Upload your first CV above!
            </div>
          ) : (
            <AnimatePresence>
              {submissions.map((submission, index) => (
                <motion.div
                  key={submission.id || `submission-${index}`}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                  transition={{ delay: index * 0.1 }}
                >
                  <GlassCard style={{ 
                    background: 'rgba(255,255,255,0.05)',
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center'
                  }}>
                    <div>
                      <div style={{ fontWeight: 600, marginBottom: '4px', color: '#fff' }}>
                         {submission.originalName}
                      </div>
                      {user?.isAdmin && submission.userEmail && (
                        <div style={{ fontSize: '14px', color: '#10b981', marginBottom: '4px' }}>
                           User: {submission.userEmail} {submission.userName && `(${submission.userName})`}
                        </div>
                      )}
                      {submission.hasJobDescription && (
                        <div style={{ fontSize: '14px', color: '#f59e0b', marginBottom: '4px', fontStyle: 'italic' }}>
                           Job Description Available
                        </div>
                      )}
                      <div style={{ fontSize: '14px', color: '#94a3b8' }}>
                        Uploaded: {new Date(submission.uploadedAt).toLocaleDateString()} | 
                        Size: {(submission.fileSize / 1024 / 1024).toFixed(2)} MB
                      </div>
                    </div>
                    <div style={{ display: 'flex', gap: '8px' }}>
                      <Button
                        onClick={() => handleDownload(submission.id, submission.originalName)}
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                      >
                         Download CV
                      </Button>
                      {submission.hasJobDescription && (
                        <Button
                          onClick={() => handleDownloadJobDescription(submission.id, submission.originalName)}
                          whileHover={{ scale: 1.05 }}
                          whileTap={{ scale: 0.95 }}
                        >
                           Job Desc
                        </Button>
                      )}
                      <Button
                        variant="danger"
                        onClick={() => handleDelete(submission.id)}
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                      >
                         Delete
                      </Button>
                    </div>
                  </GlassCard>
                </motion.div>
              ))}
            </AnimatePresence>
          )}
        </div>

        {user?.isAdmin && (
          <div style={{ 
            marginTop: '48px', 
            paddingTop: '48px', 
            borderTop: '2px solid #ffd700',
            background: 'rgba(255, 215, 0, 0.05)',
            borderRadius: '12px',
            padding: '32px'
          }}>
            <h3 style={{ color: '#ffd700', marginBottom: '24px', display: 'flex', alignItems: 'center', gap: '8px' }}>
               Admin Controls
            </h3>
            <Button
              variant="admin"
              onClick={handleDownloadAllCVs}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              style={{ marginBottom: '16px' }}
            >
               Download All CVs
            </Button>
            <Button
              variant="admin"
              onClick={handleDeleteAllCVs}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              style={{ background: 'rgba(255, 107, 107, 0.2)', borderColor: '#ff6b6b', color: '#ff6b6b' }}
            >
               Delete All CVs (DANGER)
            </Button>
          </div>
        )}
      </GlassCard>
    </Container>
  );

  if (!isAuthenticated) {
    const content = renderAuthContent();
    return isModal ? (
      <ModalOverlay
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        onClick={onClose}
      >
        <ModalContent
          initial={{ scale: 0.9, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          exit={{ scale: 0.9, opacity: 0 }}
          onClick={(e) => e.stopPropagation()}
        >
          {onClose && <CloseButton onClick={onClose}></CloseButton>}
          {content}
        </ModalContent>
      </ModalOverlay>
    ) : content;
  }

  const content = renderMainContent();
  return (
    <>
      {isModal ? (
        <ModalOverlay
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          onClick={onClose}
        >
          <ModalContent
            initial={{ scale: 0.9, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            exit={{ scale: 0.9, opacity: 0 }}
            onClick={(e) => e.stopPropagation()}
          >
            {onClose && <CloseButton onClick={onClose}></CloseButton>}
            {content}
          </ModalContent>
        </ModalOverlay>
      ) : content}
      
      {/* ATS Analysis Modal */}
      <ATSAnalysis
        isOpen={analysisModal.isOpen}
        onClose={closeAnalysisModal}
        taskId={analysisModal.taskId}
        filename={analysisModal.filename}
        token={token}
      />
    </>
  );
};

export default CVHacker;